.product-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: none;
    padding: 0;
    flex-grow: 1;
}
.product-card-body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: flex-end;
}